.header{
    margin-left: 30%;
    margin-top: 5%;
    background-color: white;
    display: inline-block;
    color: black;
}
.header1{
    margin-top: 1%;

}
nav a{
    color: white;
}
.header2{
    margin-left: 10%;
    width: 80%;
}
.group{
    background-color: white;
    width: 50%;
    margin: 0 auto;
    border: black 2px solid;
    padding: 2%;
    font-family: 'Vollkorn', serif;
    font-size: 20px;
}

.btn-dark{
    margin-left: 40%;
    font-size: 19px;
}
#input{
    width: 60% !important;
    margin-left: 20%;
}
.dark{
    margin-left: 2%;
  
    /* font-size: 17px; */
}
.dark1{
    font-size: 17px;
    margin-left: 0%;
    margin-top: 6%;
}
.form-control{
    display: inline-block;
    width: 80% !important;
    margin-left: 2%;
}
.song{
    background-color: black;
    color: white;
    display: inline-block;
    width: 19%;
    /* height: 270px; */
    margin: 3%;
    padding: 1.5%;
    text-align: center;
    font-family: 'Vollkorn', serif;
}
.image{
    margin: 0 auto;
    
    width: 150px;
    height: 150px;
}
.songName{
 
    font-size: 20px;
    margin-top: 8%;
}

.row{
    background-color: black;
    color: white;
    margin: 2%;
    text-align: center;
    font-family: 'Vollkorn', serif;
    padding: 0.5%;
    font-size: 19px;
    position: relative;
    height: 130px;
}
.row .name{
    display: block;
    margin: 2%;
    /* font-size: 20px; */
    /* width: 100%; */
}
.row .btn-dark{
    position: absolute;
    right: 0%;
    margin: 2%;
 
}
.row .listBtn{
    right: 9%;
}
.row .listBtn2{
    right: 21%;
}

#audioplayer{
    position: absolute;
    width: 100%;
    left: 0px;
    bottom: 0px;
}
#audio{
    width: 45% !important;
}
@media(max-width: 1200px){
    .row{
        font-size: 17px;
        padding: 1%;
    }
    .row .btn-dark{
        font-size: 16px;
    }
    .row .listBtn{
        right: 12%;
    }
    .row .listBtn2{
        right: 28%;
    }
}

@media(max-width: 1100px){
    .group{
        width: 60%;
    }
  
}
@media(max-width: 1000px){
    .row .name{
        margin: 0%;
        margin-left: 5%;
    }
    .row .btn-dark{
        /* left: 2%; */
        bottom: 2%;
    }
    .row .listBtn{
        right: 12%;
    }
    .row .listBtn2{
        right: 28%;
    }
    #audioplayer{
        width: 70%;
        left: 0px;
    }
    #audio{
        width: 57% !important;
    }
    .song{
        width: 23%;
        margin-left: 6%;
    }
    .songName{
        font-size: 16.5px;
    }
    .dark1{
        font-size: 15px;
    }
    .header1{
        margin-left: 21%;
    }
    .header1 h1{
        font-size: 34px;
    }
    .header2{
        margin-left: 5%;
    }
    .header2 h1{
        font-size: 30px;
    }
    #input{
        margin-left: 14%;
    }

}
@media(max-width: 900px){
    .group{
        width: 68%;
        font-size: 18px;
    }
    
    .row .btn-dark{
        /* left: 2%; */
        bottom: 2%;
    }
    .row .listBtn{
        right: 15%;
    }
    .row .listBtn2{
        right: 35%;
    }
    .navbar-nav{
        margin-left: 40%;
    }
  
  
}
@media(max-width: 700px){
    .group{
        width: 84%;
        font-size: 16px;
    }
    .btn-dark{
        font-size: 17px;
    }
    .row{
        font-size: 15.5px;
        height: 130px;
    }
    .row .btn-dark{
        font-size: 14px;
    }
    #audioplayer{
        left: 5%;
        bottom: 12%;
        /* height: 20%; */
    }
    #audio{
        padding: 0% !important;
        /* width: 50%; */
        /* height: 70%; */
    }
    .image{
        width: 120px;
        height: 120px;
    }
    .header1{
        margin-left: 3%;
    }
    .header1 h1{
        font-size: 32px;
    }
    .song{
        width: 28%;
        margin-left: 2%;
    }
    .songname{
        font-size: 15px;
    }
    .song .dark1{
        font-size: 14px;
    }
    .header2{
        margin-left: 2%;
    }
    .header2 h1{
        text-align: center;
        font-size: 24px;
    }
}
@media(max-width: 630px){
    .row{
        padding: 3%;
        height: 180px;
    }
    .row .listImage{
        width: 70px;
        height: 70px;
    }
    .row .name{
        /* margin-left: 10%; */
        position: absolute;
        font-size: 14px;
    }
    .row .name1{
        left: 23%;
    }
    .row .name2{
        left: 23%;
        top: 24%;
    }
    .row .btn-dark{
        font-size: 13px;
    }
   
    .row .listBtn{
       
        right: 27%;
    }
    /* .row .name{
        margin: 0%;
        margin-left: 3.5%;
    }
    .row .btn-dark{
        /* left: 2%; 
        bottom: 2%;
    }*/
    /* .row .listBtn{
        right: 18%;
    }
    .row .listBtn2{
        right: 40%;
    }  */
    #audioplayer{
        top: 37%;
        left: 22%;
    }
    #audio{
        width: 110% !important;
    }
}
@media(max-width: 500px){
    .form-control{
        margin-left: 0%;
        width: 100% !important;
        display: block;
    }
    
    .header1{
        margin-left: 2%;
    }
    .header1 h1{
       margin: 0 auto;
       text-align: center;
        font-size: 24px;
    }
    .song{
        width: 45%;
        padding: 2%;
    }
    #input{
        margin-left: 5%;
        margin-right: 0%;
        display: inline-block;
    }
   .dark{
       font-size: 15px;
       margin-left: 5%;
   }
   .songname, .dark1{
       font-size: 14px;
   }
   .header2{
       margin-left: 0%;
       width: 100%;
   }
   .header2 h1{
       font-size: 20px;
   }
   .header2 .btn-dark{
       font-size: 13px;
       margin-left: 2%;
   }

   
}