#home{
    position: absolute;
    top: 70%;
    left: 40%;
    background-color: white;
    color: black;
}

#home h1{
    font-family: 'Bree Serif', serif;
    text-align: center;
}

@media(max-width: 992px){
    #home{
        left: 27%;
    }
    #home h1{
        font-size: 35px;
    }
}
@media(max-width: 768px){
    #home{
        left: 22%;
    }
    #home h1{
        font-size: 30px;
    }
}
@media(max-width: 562px){
    #home{
        left: 12%;
    }
    #home h1{
        font-size: 25px;
    }
}
@media(max-width: 410px){
    #home{
        left: 1.5%;
    }
    #home h1{
        font-size: 23px;
    }
}